html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background-color: #577399;
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.slide-container {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: center;
  padding: 10px;
}

.slides {
  max-width: 40vw;
  display: flex;
  justify-content: center;
}

.slide > img {
  max-width: 100%;
  max-height: 100%;
}

.arrow-container-left,
.arrow-container-right {
  align-self: center;
}

.arrow-container-left > img,
.arrow-container-right > img {
  width: 75px;
  border: solid #bdd5ea 2px;
  border-radius: 10px;
}

.arrow-container-left > img {
  transform: rotate(90deg);
}

.arrow-container-right > img {
  transform: rotate(-90deg);
}

.slide {
  display: none;
}

.visibility {
  display: flex;
}

.dots-layout {
  height: 20%;
  width: 100%;
  display: grid;
  grid-template-columns: 3fr 5fr 3fr;
}

.dots-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.dots-container > div {
  width: 35px;
  height: 35px;
  border: solid 2px #fe5f55;
  border-radius: 50%;
}

.toggle-background {
  background-color: #fe5f55;
}
